import { useState, useCallback, useMemo, useRef, useEffect } from 'react';
import type {
  OrgWithMembership,
  Organization,
  CreateOrgRequest,
  UpdateOrgRequest,
  Permission,
  OrgRole,
  AuthError,
} from '../types';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { OrgApiClient } from '../utils/orgApi';

export interface UseOrgsReturn {
  /** All organizations the user belongs to */
  orgs: OrgWithMembership[];
  /** Currently active organization */
  activeOrg: OrgWithMembership | null;
  /** User's permissions in the active org */
  permissions: Permission[];
  /** User's role in the active org */
  role: OrgRole | null;
  /** Loading state */
  isLoading: boolean;
  /** Error state */
  error: AuthError | null;
  /** Fetch/refresh organizations list */
  fetchOrgs: () => Promise<void>;
  /** Switch to a different organization */
  switchOrg: (orgId: string) => Promise<void>;
  /** Create a new organization */
  createOrg: (data: CreateOrgRequest) => Promise<Organization>;
  /** Update an organization */
  updateOrg: (orgId: string, data: UpdateOrgRequest) => Promise<Organization>;
  /** Delete an organization */
  deleteOrg: (orgId: string) => Promise<void>;
  /** Check if user has a specific permission */
  hasPermission: (permission: Permission) => boolean;
}

const ACTIVE_ORG_KEY = 'cedros_active_org';

// P-06: Safe localStorage helpers to handle private browsing and quota errors
function safeGetItem(key: string): string | null {
  try {
    return localStorage.getItem(key);
  } catch {
    return null;
  }
}

function safeSetItem(key: string, value: string): void {
  try {
    localStorage.setItem(key, value);
  } catch {
    // Ignore - private browsing or quota exceeded
  }
}

/**
 * Hook for managing organizations, memberships, and permissions.
 *
 * @example
 * ```tsx
 * function OrgSelector() {
 *   const { orgs, activeOrg, switchOrg, hasPermission } = useOrgs();
 *
 *   return (
 *     <select
 *       value={activeOrg?.id}
 *       onChange={(e) => switchOrg(e.target.value)}
 *     >
 *       {orgs.map(org => (
 *         <option key={org.id} value={org.id}>{org.name}</option>
 *       ))}
 *     </select>
 *   );
 * }
 * ```
 */
export function useOrgs(): UseOrgsReturn {
  const { config, user, authState, _internal } = useCedrosLogin();
  const hasStorage = typeof window !== 'undefined' && !!window.localStorage;

  const [orgs, setOrgs] = useState<OrgWithMembership[]>([]);
  const [activeOrg, setActiveOrg] = useState<OrgWithMembership | null>(null);
  const [permissions, setPermissions] = useState<Permission[]>([]);
  const [role, setRole] = useState<OrgRole | null>(null);
  const [isLoading, setIsLoading] = useState(authState === 'authenticated');
  const [error, setError] = useState<AuthError | null>(null);

  // M-03: Memoize API client and use ref to prevent callback dependency cascades
  const apiClient = useMemo(
    () =>
      new OrgApiClient(
        config.serverUrl,
        config.requestTimeout,
        config.retryAttempts,
        _internal?.getAccessToken
      ),
    [config.serverUrl, config.requestTimeout, config.retryAttempts, _internal]
  );

  // M-03: Store apiClient in ref to stabilize callback dependencies
  const apiClientRef = useRef(apiClient);
  useEffect(() => {
    apiClientRef.current = apiClient;
  }, [apiClient]);

  // M-03: Use ref in callback to break dependency chain
  const loadPermissions = useCallback(async (orgId: string) => {
    try {
      const response = await apiClientRef.current.getPermissions(orgId);
      setPermissions(response.permissions);
      setRole(response.role);
    } catch {
      // Permissions loading failure is non-fatal, just clear them
      setPermissions([]);
      setRole(null);
    }
  }, []);

  // Ref to latest fetchOrgs for stable auto-fetch effect
  const fetchOrgsRef = useRef<() => Promise<void>>(async () => {});

  // M-03: Use ref to break apiClient dependency chain
  const fetchOrgs = useCallback(async () => {
    if (authState !== 'authenticated' || !user) {
      setOrgs([]);
      setActiveOrg(null);
      setPermissions([]);
      setRole(null);
      return;
    }

    setIsLoading(true);
    setError(null);

    try {
      const fetchedOrgs = await apiClientRef.current.listOrgs();
      setOrgs(fetchedOrgs);

      // P-06: Restore active org from localStorage with safe access
      const savedOrgId = hasStorage ? safeGetItem(ACTIVE_ORG_KEY) : null;
      let selectedOrg = fetchedOrgs.find((org) => org.id === savedOrgId);

      if (!selectedOrg && fetchedOrgs.length > 0) {
        // Default to personal org or first org
        selectedOrg = fetchedOrgs.find((org) => org.isPersonal) || fetchedOrgs[0];
      }

      if (selectedOrg) {
        setActiveOrg(selectedOrg);
        if (hasStorage) {
          safeSetItem(ACTIVE_ORG_KEY, selectedOrg.id);
        }
        await loadPermissions(selectedOrg.id);
      } else {
        setActiveOrg(null);
        setPermissions([]);
        setRole(null);
      }
    } catch (err) {
      setError(err as AuthError);
    } finally {
      setIsLoading(false);
    }
  }, [authState, user, loadPermissions, hasStorage]);

  // Keep fetchOrgsRef current so auto-fetch always calls the latest version
  useEffect(() => {
    fetchOrgsRef.current = fetchOrgs;
  }, [fetchOrgs]);

  // Auto-fetch orgs when auth becomes ready.
  // Uses only `authState` (a string) as dep â€” immune to object-identity churn
  // from the AdminShell bridge recreating `user` objects.
  const hasAutoFetched = useRef(false);
  useEffect(() => {
    if (authState === 'authenticated' && !hasAutoFetched.current) {
      hasAutoFetched.current = true;
      fetchOrgsRef.current();
    } else if (authState !== 'authenticated') {
      hasAutoFetched.current = false;
    }
  }, [authState]);

  const switchOrg = useCallback(
    async (orgId: string) => {
      const org = orgs.find((o) => o.id === orgId);
      if (!org) {
        setError({ code: 'UNKNOWN_ERROR', message: 'Organization not found' });
        return;
      }

      setActiveOrg(org);
      if (hasStorage) {
        safeSetItem(ACTIVE_ORG_KEY, orgId);
      }
      await loadPermissions(orgId);
    },
    [orgs, loadPermissions, hasStorage]
  );

  // M-03: Use ref to break apiClient dependency chain
  const createOrg = useCallback(
    async (data: CreateOrgRequest): Promise<Organization> => {
      setIsLoading(true);
      setError(null);

      try {
        const newOrg = await apiClientRef.current.createOrg(data);
        // Refresh orgs list to include the new org with membership
        await fetchOrgs();
        return newOrg;
      } catch (err) {
        setError(err as AuthError);
        throw err;
      } finally {
        setIsLoading(false);
      }
    },
    [fetchOrgs]
  );

  const updateOrg = useCallback(
    async (orgId: string, data: UpdateOrgRequest): Promise<Organization> => {
      setIsLoading(true);
      setError(null);

      try {
        const updatedOrg = await apiClientRef.current.updateOrg(orgId, data);
        // Refresh orgs list to reflect changes
        await fetchOrgs();
        return updatedOrg;
      } catch (err) {
        setError(err as AuthError);
        throw err;
      } finally {
        setIsLoading(false);
      }
    },
    [fetchOrgs]
  );

  const deleteOrg = useCallback(
    async (orgId: string): Promise<void> => {
      setIsLoading(true);
      setError(null);

      try {
        await apiClientRef.current.deleteOrg(orgId);
        // Refresh orgs list
        await fetchOrgs();
      } catch (err) {
        setError(err as AuthError);
        throw err;
      } finally {
        setIsLoading(false);
      }
    },
    [fetchOrgs]
  );

  const hasPermission = useCallback(
    (permission: Permission): boolean => {
      return permissions.includes(permission);
    },
    [permissions]
  );

  return {
    orgs,
    activeOrg,
    permissions,
    role,
    isLoading,
    error,
    fetchOrgs,
    switchOrg,
    createOrg,
    updateOrg,
    deleteOrg,
    hasPermission,
  };
}
